  height: 60px;
  top: 50%;
  left: 50%;
  margin: -30px 0 0 -30px;
  border-radius: 30px;
  background: rgba(0, 0, 0, 0.15);
  color: white;
  animation-duration: 600ms;
  animation-fill-mode: both;
  animation-delay: 900ms;
  animation-name: fadeOut; }

@keyframes fadeOut {
