html,
body {
	width: 100%;
	height: 100%;
	padding: 0;
	margin: 0;
	overflow-x: hidden;
}

body {
	background: $white;
	color: $dark;
	-webkit-overflow-scrolling: touch;

	&.opened {
		height: 100vh !important;
		overflow: hidden;
	}
}

.container--big {
	max-width: calc(100vw - #{$col-width});
	margin: 0 auto;
}

.container--mid {
	max-width: calc(100vw - #{$col-width-2});
	margin: 0 auto;

	@include xs-down {
		max-width: calc(100vw - #{$col-width});
	}
}

.container--small {
	max-width: calc(100vw - #{$col-width-6});
	margin: 0 auto;

	@include md-down {
		max-width: calc(100vw - #{$col-width-2});
	}

	@include xs-down {
		max-width: calc(100vw - #{$col-width});
	}
}


[data-shadow] {
	box-shadow: 10px 20px 20px -8px rgba(0, 0, 0, 0.15);
}

[unselectable] {
	-webkit-touch-callout: none;
	user-select: none;
}

::-webkit-scrollbar {
	width: 4px;
	height: 4px;
	background-color: #f3f3f3;
	border-radius: 2px;
}

::-webkit-scrollbar-thumb {
	// background: #c00019;
	background: darken(#f3f3f3, 10%);
	border-radius: 2px;
}

.svg-sprites {
	height: 0;
	width: 0;
	position: absolute;
	visibility: hidden;
}
